<template>
  <transition-group tag="form" name="auth" class="card">
    <h3 key="header1">Авторизация</h3>
    <!-- Ввод домена -->
    <div key="host1" v-if="!fromsSelected.length" class="input-field">
      <input
        id="link"
        type="text"
        v-model="host"
        autocomplete="off"
        :class="{
          invalid: oldValue.host != null && !suitedHost && oldValue.host == host
        }"
      />
      <label for="link">Домен</label>
      <span class="helper-text" :data-error="message.error">
        {{ message.default }}
      </span>
    </div>
    <!-- Выбор школы -->
    <div
      v-for="select in fromsSelected"
      :key="select.id"
      class="input-field"
      :name="select.name"
    >
      <select
        class="browser-default"
        :name="select.name"
        v-model="select.value"
        @change="selected(select.name)"
        :data-fn="checkOptions(select)"
      >
        <option
          v-for="option in select.options"
          :key="select.name + option.id"
          :value="option.id"
          :disabled="option.id == null"
          >{{ option.name }}</option
        >
      </select>
    </div>
    <!-- Логин и пароль -->
    <div
      key="login1"
      class="input-field"
      v-if="
        fromsSelected.length && fromsSelected[fromsSelected.length - 1].value
      "
    >
      <input
        id="login"
        type="text"
        v-model="login"
        autocomplete="login"
        :class="{
          invalid:
            oldValue.login != null &&
            ((authError == false && !login) ||
              (authError == true && oldValue.login == login))
        }"
      />
      <label for="login">Логин</label>
      <span
        class="helper-text"
        :data-error="authError ? 'Тут ошибочка' : 'Ты забыл ввести логин'"
        >Введи логин</span
      >
    </div>
    <div
      key="password1"
      class="input-field"
      v-if="
        fromsSelected.length && fromsSelected[fromsSelected.length - 1].value
      "
    >
      <input
        id="password"
        type="password"
        v-model="password"
        autocomplete="password"
        :class="{
          invalid:
            oldValue.password != null &&
            ((authError == false && !password) ||
              (authError == true && oldValue.password == password))
        }"
      />
      <label for="password">Пароль</label>
      <span
        class="helper-text"
        :data-error="authError ? 'Тут ошибочка' : 'Ты забыл ввести пароль'"
        >Введи пароль</span
      >
    </div>
    <!-- Кнопка -->
    <button
      key="btn1"
      @click="sudmit"
      v-if="
        fromsSelected.length == 0 ||
          fromsSelected[fromsSelected.length - 1].value
      "
      :disabled="attemptingLogin"
      class="btn waves-effect waves-light light-blue darken-1"
    >
      {{
        !fromsSelected.length
          ? "Проверить"
          : attemptingLogin
          ? "Подожди..."
          : "Войти"
      }}
      <i class="material-icons right">arrow_forward</i>
    </button>
  </transition-group>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data: () => ({
    host: "",
    login: "",
    password: "",
    message: {
      default: "Введи ссылку на дневник",
      error: ""
    },
    oldValue: {
      host: null,
      login: null,
      password: null
    },
    authError: null,
    suitedHost: null,
    attemptingLogin: false,
    fromsSelected: []
  }),
  methods: {
    ...mapActions(["SendMessage"]),
    // Отправка формы
    sudmit(e) {
      e.preventDefault();
      if (this.fromsSelected.length == 0) this.checkHost();
      else this.logIn();
    },
    // Вход в систему
    logIn() {
      this.authError = null;
      let ttsLogin = "";
      const selectors = this.fromsSelected;
      const login = (this.oldValue.login = this.login);
      const password = (this.oldValue.password = this.password);
      if (!selectors[selectors.length - 1].value) return;
      if (!login || !password) return (this.authError = false);
      this.attemptingLogin = true;
      for (const selector of selectors) {
        if (!selector) continue;
        if (ttsLogin) ttsLogin += "&";
        ttsLogin += `${selector.name}=${selector.value}`;
      }
      this.SendMessage({
        method: "AddUser",
        data: {
          host: this.host,
          username: login,
          password,
          ttsLogin
        }
      })
        .then(
          ({ id }) => ((localStorage.uid = id), this.$router.replace("diary"))
        )
        .catch(err => {
          if (err.code == 4007) {
            this.authError = true;
            this.attemptingLogin = false;
          } else if (err.code == 4008) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Сайт лег, попробуй позже" });
          } else if (err.code == 4006) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
          } else {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
          }
          console.error(err);
        });
    },
    // Проверка хоста
    checkHost() {
      const oldMessage = this.message.default;
      if (!this.host) {
        this.oldValue.host = "";
        this.message.error = "Ты забыл ввести ссылку";
        return;
      }

      this.suitedHost = true;
      this.message.default = "Проверяю домен, подожди...";
      this.oldValue.host = this.host = this.host.replace(
        /https*:*\/*|\/.*/g,
        ""
      );
      this.SendMessage({
        method: "CheckHost",
        data: { host: this.host }
      })
        .then(({ canWork }) => {
          this.suitedHost = canWork;
          this.message.default = oldMessage;
          if (!canWork) {
            this.message.default = oldMessage;
            this.message.error = "Я не могу работать с этим сайтом";
            return;
          }
          this.loadForm();
        })
        .catch(err => {
          this.message.default = oldMessage;
          this.message.error = err.msg;
          this.suitedHost = false;
          console.error(err);
        });
    },
    // Загрузка формы авторизации
    loadForm() {
      const oldMessage = this.message.default;
      this.message.default = "Получаю форму авторизации, подожди...";
      this.SendMessage({
        method: "AuthForm",
        data: { host: this.host }
      })
        .then(({ selectors }) => {
          this.fromsSelected = selectors;
          this.message.default = oldMessage;
        })
        .catch(err => {
          this.message.default = oldMessage;
          this.message.error = err.msg;
          this.suitedHost = false;
          console.error(err);
        });
    },
    // Выбор школы и т.д.
    selected(name) {
      const data = {};
      const index = this.fromsSelected.findIndex(s => s.name == name) + 1;
      if (index == this.fromsSelected.length) return;
      for (let i = index; i < this.fromsSelected.length; i++) {
        this.fromsSelected[i].value = null;
        this.fromsSelected[i].options = [{ id: null, name: "" }];
      }
      for (let i = 0; i < index; i++) {
        let { name, value } = this.fromsSelected[i];
        data[name] = value;
      }
      this.SendMessage({
        method: "SelectedData",
        data: {
          host: this.host,
          lastElem: name,
          selected: data
        }
      })
        .then(({ items }) => (this.fromsSelected[index].options = items))
        .catch(console.error);
    },
    // Проверка опций селектора
    checkOptions(select) {
      if (!select.options.find(o => o.id == select.value)) {
        select.value = null;
        select.options.unshift({ id: null, name: "Тут нужно выбрать" });
      }
    }
  },
  beforeCreate() {
    fetch(`/exist/${localStorage.uid}`)
      .then(res => res.json())
      .then(exist => {
        if (exist) this.$router.replace("diary");
      });
  }
};
</script>

<style scoped>
@import "../assets/login.css";
</style>
